import React from 'react';
import ReactDOM from 'react-dom';
import './App.css';

export class Screen extends React.Component {
    constructor(props) {
        super(props)
        
        this.state = {
            circleStyle: {
                display: "none",
                marginLeft: 0,
                marginTop: 0,
                borderColor: "black"
            }
        }
        
        this.box = React.createRef()
        this.circle = React.createRef()
        
        this.createCircle = this.createCircle.bind(this)
        this.handleScreenClick = this.handleScreenClick.bind(this)
    }
    
    createCircle() {
        const coords = this.box.current.getBoundingClientRect()
        
        this.setState({
            circleStyle: {
                display: "inline-block",
                marginLeft: Math.random()*(coords.width-40)+10,
                marginTop: Math.random()*(coords.height-40)+10
            }
        })   
        
    }
    
    handleScreenClick(e) {
        const circleCoords = this.circle.current.getBoundingClientRect()
        const circleCenter = [circleCoords.x + circleCoords.width/2, circleCoords.y + circleCoords.height/2 + window.scrollY]
        
        //alert("~ " + e.pageX + " | " + circleCenter + " | " + e.pageY + " ~")
        
        this.props.onMouseDown(Math.pow(Math.pow(e.pageX-circleCenter[0],2) + Math.pow(e.pageY-circleCenter[1],2),0.5))
        
        this.setState({
            circleStyle: {
                display: "none",
                marginLeft: 10,
                marginTop: 10
            }
        })
    }
    
        
    
    render() {
        
        return (
                <div>
                    <div 
                        className="game" 
                        onMouseDown={this.handleScreenClick} 
                        onMouseMove={this.props.onMouseMove}
                        ref={this.box}
                    >
                        <div><span className="circle" style={this.state.circleStyle} ref={this.circle}></span></div>
                    </div>
                    <button className="button" onClick={this.createCircle}>Give Circle</button>
                </div>)
    }
}

export default Screen